<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-companies',
  data () {
    return {
      config: {
        title: 'Perusahaan',
        model_api: 'companies',
        getter: 'companies',
        setter: 'companies',
        pk_field: null,
        permission: {
          create: 'create-companies',
          read: 'view-companies',
          update: 'update-companies',
          delete: 'delete-companies'
        },
        slave: [],
        redirectAfterForm: { name: 'perusahaan' },
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'status_code', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'primary_address', label: 'Alamat Utama', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'secondary_address', label: 'Alamat Sekunder', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'company_name', label: 'Nama Perusahaan' },
          { id: 'company_type', label: 'Jenis Perusahaan' },
          { id: 'postal_code', label: 'Kode POS' },
          { id: 'primary_telephone', label: 'Telephone Utama' },
          { id: 'secondary_telephone', label: 'Telephone Sekunder' },
          { id: 'email', label: 'Email' },
          { id: 'url_website', label: 'Website Perusahaan' },
          { id: 'nomor_nib', label: 'Nomor NIB' },
          { id: 'nomor_npwp', label: 'Nomor NPWP' },
          {
            id: 'img_logo',
            label: 'Logo Perusahaan',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_logo.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'img_logo'
              }
            }
          }
        ]
      }
    }
  }
}
</script>



